import { Component, EventEmitter, HostBinding, Input, Output } from '@angular/core';

@Component({
  selector: 'rx-blade',
  templateUrl: './blade.component.html',
  styleUrls: ['blade.component.scss']
})
export class RxBladeComponent {
  @HostBinding('class.collapsed')
  isBladeCollapsed = false;

  @Input()
  title = '';

  @Input()
  set isExpanded(value: boolean) {
    this.isBladeExpanded = value;
    this.isBladeCollapsed = !value;
  }

  get isExpanded() {
    return this.isBladeExpanded;
  }

  @Input()
  @HostBinding('class')
  dockTo: 'left' | 'right' = 'left';

  @Output()
  toggle: EventEmitter<any> = new EventEmitter();

  private isBladeExpanded = true;

  toggleBlade(): void {
    this.toggle.emit();
  }
}
